import "./App.css";
import React, { useContext, useEffect, useRef, useState } from "react";
import { AuthContext } from "./Components/Auth/Context/AuthContext";
import Login from "./Components/Auth/Login/Login";
import { useSelector } from "react-redux";
import { createSelector } from "reselect";
import { RootState } from "./Store";
import { auth } from "./firebase";
import { InputBase, IconButton } from "@mui/material";
import { writeChat } from "./FirebaseCalls/FirebaseCalls";
import { ChatMessage } from "./Models/ChatMessage";
import { useParams } from "react-router-dom";
import { Message } from "./Components/Messages/Messages";
import SendIcon from "@mui/icons-material/Send";
import useChatsHook from "./FirebaseCalls/useChatListener";

export default function TestChatView() {
  const user = useContext(AuthContext);

  return (
    <div className="App">
      {/* <header>
        <h1>NuePlatform</h1>
      </header> */}

      <section>{user ? <ChatRoom /> : <Login />}</section>
    </div>
  );
}

function ChatRoom() {
  const { id } = useParams();

  const categories = useSelector(
    (state: RootState) => state.selectedCategoryReducer
  );

  const showMessages = createSelector(
    (state: RootState) => state.chatsState.messages,
    (messages) =>
      messages.filter(
        (message) =>
          message.Category ===
            categories.categories[categories.category]["categorizedName"] ||
          categories.categories[categories.category]["categorizedName"] ===
            "All"
      )
  );

  const messages = useSelector(showMessages);

  useChatsHook();

  const [msg, setMsg] = useState("");

  const sendHandler = () => {
    writeChat({
      Message: msg,
      InfluencerId: auth.currentUser?.uid ?? id,
      SentBy: auth.currentUser ? auth.currentUser!.uid : "",
      Date: new Date().valueOf(),
    } as ChatMessage);
    setMsg("");
  };

  useEffect(() => {
    const chatElement = document.getElementById("chat");
    if (chatElement) {
      chatElement.scrollTop = chatElement.scrollHeight;
    }
  });

  return (
    <>
      <div id="chat" className="main">
        {messages &&
          messages.map((msg) => (
            // <Message influencerId={msg.InfluencerId} chatMessage={msg} />
            <GESMESSAGE uid={msg.InfluencerId} text={msg.Message}></GESMESSAGE>
          ))}
      </div>

      <form>
        <InputBase
          placeholder="Write here.. (everything is anonymous)"
          value={msg}
          onChange={(e) => setMsg(e.target.value)}
          onKeyDown={(e) => {
            if (e.key === "Enter") {
              sendHandler();
            }
          }}
        />
        <IconButton onClick={sendHandler}>
          <SendIcon />
        </IconButton>
      </form>
    </>
  );
}

interface ChatMessageProps {
  text: string;
  uid: string;
}

export const GESMESSAGE: React.FC<ChatMessageProps> = (props) => {
  const messageClass =
    props.uid === auth.currentUser?.uid ? "sent" : "received";

  return (
    <>
      <div className={`message ${messageClass}`}>
        <p>{props.text}</p>
      </div>
    </>
  );
};
